/***********************************************/
/************** MENU DE NAVEGAÇÃO *************/
/**********************************************/

#navbar {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    z-index: 1;
  }
  
  nav {
    background: #ffffff00;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 100px;
    font-family: "Nunito", sans-serif;
  }
  
  nav .logo {
    color: #310052;
    font-size: 35px;
    font-weight: 600;
    margin-left: -6% ;
    font-family: "Nunito", sans-serif;
    letter-spacing: -1px;
  }

  nav .nav-items {
    display: flex;
    flex: 1 1;
    padding: 0 0 0 40px;
    font-size: 16px;
    margin-top:0.4%;
  }
  nav .nav-items button {
    list-style: none;
    padding: 0 15px;
  }
  
  nav form {
    display: flex;
    height: 40px;
    padding: 2px;
    background: #ffffff;
    min-width: 18% !important;
    border-radius: 2px;
    border: 1px solid rgba(155, 155, 155, 0.2);
  }
  


  /***********************************************************/
  /*************************BOTOES NAV***********************/
  /**********************************************************/
  
  .buttonNav {
    font-family: "Nunito", sans-serif;
    font-size: 1.2em;
    font-weight: 900;
    color: black;
    z-index: 2;
    background: none;
    border: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
    background: linear-gradient(to right, crimson, pink, springgreen);
    -webkit-animation: rainbow 2s ease-in-out infinite;
            animation: rainbow 2s ease-in-out infinite;
    background-clip: text;
    -webkit-background-clip: text;
    color: rgba(0, 0, 0, 1);
    display: block;
    transition: color 0.5s ease-in-out;
  }
  
  .buttonNav:hover {
    color: rgba(0, 0, 0, 0);
  }
  
  /********************************************/
  /************** USER BUTTON ON NAV *************/
  /**********************************************/
  .dropdownUser {
    position: relative;
    
    z-index: 3;
    margin-left: 0%;
  }
  
  .dropdown-contentUser {
    display: none;
    position: absolute;
    background-color: #ffffff;
    min-width: 80px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 3;
  }
  
  .dropdown-contentUser button {
    font-family: "Nunito", sans-serif;
    font-size: 1.2em;
    background: none;
    border: none;
    cursor: pointer;
    display: block;
    z-index: 3;
    margin: 20px;
    width: -webkit-max-content;
    width: max-content;
  }
  
  .dropdown-contentUser button:hover {
    text-decoration: underline;
    cursor: pointer;
    z-index: 3;
  }
  
  .dropdownUser:hover .dropdown-contentUser {
    display: block;
  }
  
  .dropdownUser:hover {
    cursor: pointer;
    color: #7cc679;
  }
  
  /********************************************/
  /************** user Profile Button*************/
  /**********************************************/
  
  .userProfileButton,
  .LogoutButton {
    background-color: #fafafa;
    color: rgb(0, 0, 0);
    border-radius: 5px;
    padding: 16px;
    font-size: 16px;
    border: none;
    margin-right: -0.5em;
    padding-top: -2em;
    padding-bottom: -2em;
  }
  
  .LogoutButton:hover {
    cursor: pointer;
    color: #2ab1b6;
  }
  
  .userProfileButton:hover {
    cursor: pointer;
    color: #682acb;
  }
  
  /********************************************/
  /************** MENU DROPDOWN*************/
  /**********************************************/
  .dropbtn {
    background-color: #fafafa;
    color: rgb(0, 0, 0);
    border-radius: 5px;
    padding: 16px;
    font-size: 18px;
    border: none;
    margin-right: -4em;
    padding-top: -2em;
    padding-bottom: -2em;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
    z-index: 3;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #ffffff;
    min-width: 50px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 3;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    z-index: 3;
  }
  
  .dropdown-content a:hover {
    background-color: #ddd;
    text-decoration: underline;
    z-index: 3;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  .dropdown:hover .dropbtn {
    cursor: pointer;
    color: #7cc679;
  }
  
  /**********************************************************/
  /************************ BOTOES LINGUA *******************/
  /*******************************************************/


  .dropdown-content img {
    pointer-events: none;
    max-width: 55%;
    max-height: 55%;
    object-fit: contain;
    margin: 0.5em;
  
  }
  .en,
  .pt {
    width: 100%;
    border: none;
    background-color: #feffff;
  }
  
  .en:hover,
  .pt:hover {
    cursor: pointer;
    background-color: #08040141;
  }
